
import React, { Component } from 'react'
import MyNavbar from './components/Navbar'

import { ForceGraph } from './components/graphing/ForceGraph'
import data from './data/sample.json'
import Subjects from './components/Subjects'
import SemesterPlan from './components/Semesterplan'

class App extends Component {
  state = {
    subs: [],
	topsorted: []
	// selected: [],
    // thisfunc: undefined,
  };

  //
  addSubs = (e) => {
    const node = e.currentTarget.getAttribute("data-item");
    const temp = data.nodes.filter((a) => a.code === node)[0];

    const { subs, thisfunc } = this.state;


	
    if (!subs.includes(temp)) {
      this.setState({
        subs: [...this.state.subs, temp],
      });
    }
	
	else {
		this.setState({
			subs: subs.filter((sub, _) => {
				return sub.id !== temp.id;
			}),
		});
	}
	thisfunc()
	
  };

  //
  removeSubs = (e) => {
    const node = e.currentTarget.getAttribute("data-item");
    const temp = data.nodes.filter((a) => a.code === node)[0];

    const { subs, thisfunc } = this.state;
	
	console.log("remove?", temp, subs)

    this.setState({
      subs: subs.filter((sub, _) => {
        return sub.id !== temp.id;
      }),
    });
	thisfunc();
  };

  //
  setFunc = (e) => {
    this.setState({
      thisfunc: e,
    });
  };
  
    //
  tsFunc = (e) => {
    this.setState({
      topsorted: e,
    });
  };

  //
  nodeHoverTooltip = (node) => {
	return node.id + " " + node.title
  }

  render() {
    const { subs, topsorted, selected, thisfunc } = this.state;

    return (
      <div className="App">
        <MyNavbar></MyNavbar>
        <div className="app-container">
          <section className="left">
            <Subjects
				myfunc={this.addSubs}
				nodesData={data.nodes}
				selected={subs}
			/>
			{/* <div className="spacer"/> */}
            {/* <Subjects myfunc={this.removeSubs} nodesData={subs} /> */}
            {/* <button onClick={thisfunc}>CLICK!!!</button> */}
          </section>
		  
		  <section className="middle">
			{/* <SemesterPlan
				nodesData={subs}
				linksData={data.links}
				result={topsorted}
				tsFunc={this.tsFunc}
			/> */}
		  </section>

          <section className="right">
            <ForceGraph
              linksData={data.links}
              nodesData={subs}
              nodeHoverTooltip={this.nodeHoverTooltip}
              mytemp={this.setFunc}
            />
          </section>
        </div>
      </div>
    );
  }
}

export default App